.card-list{
    width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}
@media (min-width: 600px) {
    .card-list { grid-template-columns: repeat(2, 1fr); }
  }
  @media (min-width: 900px) {
    .card-list { grid-template-columns: repeat(4, 1fr); }
  }
 